@keyframes f_fadeG {
  0% {
    background-color: rgb(0, 0, 0);
  }
  100% {
    background-color: rgb(255, 255, 255);
  }
}
.G {
  position: absolute;
  width: 22px;
  height: 22px;
  right: -3px;
  top: -3px;
  transform: scale(0.6);
}
.f_circleG {
  position: absolute;
  background-color: rgb(255, 255, 255);
  height: 4px;
  width: 4px;
  border-radius: 2px;
  animation-name: f_fadeG;
  animation-duration: 1.2s;
  animation-iteration-count: infinite;
  animation-direction: normal;
}
#frotateG_01 {
  left: 0;
  top: 9px;
  animation-delay: 0.45s;
}
#frotateG_02 {
  left: 3px;
  top: 3px;
  animation-delay: 0.6s;
}
#frotateG_03 {
  left: 9px;
  top: 0;
  animation-delay: 0.75s;
}
#frotateG_04 {
  right: 3px;
  top: 3px;
  animation-delay: 0.9s;
}
#frotateG_05 {
  right: 0;
  top: 9px;
  animation-delay: 1.05s;
}
#frotateG_06 {
  right: 3px;
  bottom: 3px;
  animation-delay: 1.2s;
}
#frotateG_07 {
  left: 9px;
  bottom: 0;
  animation-delay: 1.35s;
}
#frotateG_08 {
  left: 3px;
  bottom: 3px;
  animation-delay: 1.5s;
}
